import { Component, OnInit } from '@angular/core';
import { visibility, flyInOut } from "../animations/app.animation";

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    visibility(),
    flyInOut()
  ]
})
export class SignupComponent implements OnInit {
  visibility = 'shown';
  loader: boolean;
  page: boolean;
  constructor() { }

  ngOnInit(): void {
    this.visibility = 'hidden';
    this.loader = false;
    this.page = true;
    setTimeout(() => {
      this.removeLoader();
      this.visibility = 'shown';
    }, 5000 );
  }
  removeLoader() {
    this.loader = true;
      this.page = false;
  }
  
}
